'use client';

import { useEffect, useState } from 'react';
import Layout from '@/components/Layout';
import { documentsApi, comparisonApi } from '@/lib/api';
import toast from 'react-hot-toast';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

interface Document {
  id: number;
  filename: string;
  document_type: string;
}

export default function ComparePage() {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [doc1Id, setDoc1Id] = useState<number | null>(null);
  const [doc2Id, setDoc2Id] = useState<number | null>(null);
  const [comparison, setComparison] = useState<any>(null);
  const [priceChanges, setPriceChanges] = useState<any[]>([]);
  const [selectedVendor, setSelectedVendor] = useState<string>('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchDocuments();
  }, []);

  const fetchDocuments = async () => {
    try {
      const data = await documentsApi.getAll(0, 100);
      setDocuments(data.documents || []);
    } catch (error) {
      toast.error('Failed to load documents');
    }
  };

  const handleCompare = async () => {
    if (!doc1Id || !doc2Id) {
      toast.error('Please select both documents');
      return;
    }
    setLoading(true);
    try {
      const result = await comparisonApi.compare(doc1Id, doc2Id);
      setComparison(result);
    } catch (error) {
      toast.error('Failed to compare documents');
    } finally {
      setLoading(false);
    }
  };

  const handlePriceChanges = async () => {
    if (!selectedVendor) {
      toast.error('Please select a vendor');
      return;
    }
    setLoading(true);
    try {
      const changes = await comparisonApi.getPriceChanges(selectedVendor);
      setPriceChanges(changes);
    } catch (error) {
      toast.error('Failed to load price changes');
    } finally {
      setLoading(false);
    }
  };

  const vendors = Array.from(new Set(documents.map((d: any) => d.extracted_data?.vendor).filter(Boolean)));

  return (
    <Layout>
      <div className="space-y-6">
        <h1 className="text-3xl font-bold text-gray-900">Document Comparison</h1>

        {/* Document Comparison */}
        <div className="bg-white shadow rounded-lg p-6">
          <h2 className="text-lg font-medium text-gray-900 mb-4">Compare Two Documents</h2>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Document 1</label>
              <select
                value={doc1Id || ''}
                onChange={(e) => setDoc1Id(Number(e.target.value))}
                className="w-full border border-gray-300 rounded-md px-3 py-2"
              >
                <option value="">Select document...</option>
                {documents.map((doc) => (
                  <option key={doc.id} value={doc.id}>
                    {doc.filename}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Document 2</label>
              <select
                value={doc2Id || ''}
                onChange={(e) => setDoc2Id(Number(e.target.value))}
                className="w-full border border-gray-300 rounded-md px-3 py-2"
              >
                <option value="">Select document...</option>
                {documents.map((doc) => (
                  <option key={doc.id} value={doc.id}>
                    {doc.filename}
                  </option>
                ))}
              </select>
            </div>
          </div>
          <button
            onClick={handleCompare}
            disabled={loading || !doc1Id || !doc2Id}
            className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
          >
            {loading ? 'Comparing...' : 'Compare Documents'}
          </button>

          {comparison && (
            <div className="mt-6 space-y-6">
              <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                <div>
                  <h3 className="text-md font-medium text-gray-900 mb-2">Document 1</h3>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <pre className="text-sm">{JSON.stringify(comparison.document1, null, 2)}</pre>
                  </div>
                </div>
                <div>
                  <h3 className="text-md font-medium text-gray-900 mb-2">Document 2</h3>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <pre className="text-sm">{JSON.stringify(comparison.document2, null, 2)}</pre>
                  </div>
                </div>
              </div>
              {comparison.differences && comparison.differences.length > 0 && (
                <div>
                  <h3 className="text-md font-medium text-gray-900 mb-2">Differences</h3>
                  <div className="overflow-x-auto">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50">
                        <tr>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Field</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Document 1</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Document 2</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Difference</th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {comparison.differences.map((diff: any, index: number) => (
                          <tr key={index}>
                            <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                              {diff.field}
                            </td>
                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{String(diff.value1)}</td>
                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{String(diff.value2)}</td>
                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                              {diff.difference ? `$${diff.difference.toFixed(2)}` : 'N/A'}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>

        {/* Price Changes */}
        <div className="bg-white shadow rounded-lg p-6">
          <h2 className="text-lg font-medium text-gray-900 mb-4">Price Change Detection</h2>
          <div className="flex space-x-4">
            <select
              value={selectedVendor}
              onChange={(e) => setSelectedVendor(e.target.value)}
              className="flex-1 border border-gray-300 rounded-md px-3 py-2"
            >
              <option value="">Select vendor...</option>
              {vendors.map((vendor) => (
                <option key={vendor} value={vendor}>
                  {vendor}
                </option>
              ))}
            </select>
            <button
              onClick={handlePriceChanges}
              disabled={loading || !selectedVendor}
              className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 disabled:opacity-50"
            >
              {loading ? 'Loading...' : 'Analyze'}
            </button>
          </div>

          {priceChanges.length > 0 && (
            <div className="mt-6">
              <h3 className="text-md font-medium text-gray-900 mb-4">Price Changes for {selectedVendor}</h3>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={priceChanges}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="to_date" />
                  <YAxis />
                  <Tooltip />
                  <Legend />
                  <Line type="monotone" dataKey="to_amount" stroke="#3b82f6" strokeWidth={2} name="Amount ($)" />
                </LineChart>
              </ResponsiveContainer>
              <div className="mt-4 overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">From Date</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">To Date</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">From Amount</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">To Amount</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Change</th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">% Change</th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {priceChanges.map((change, index) => (
                      <tr key={index}>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{change.from_date}</td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{change.to_date}</td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          ${change.from_amount?.toFixed(2)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          ${change.to_amount?.toFixed(2)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          ${change.change?.toFixed(2)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {change.percent_change?.toFixed(2)}%
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
      </div>
    </Layout>
  );
}

